.filter-container {
    padding: rem(40px);
    box-shadow: -1px 1px 2px 0px #1783ff7d;
    margin: 0 auto;
    width: 90%;
    transition: 0.1s;
    align-items: baseline;

    &:hover {
        box-shadow: -1px 1px 5px 0px #1783ff7d;
    }
}

.toy-card {
    background-color: complement($color: #1783ff23) !important;
    transition: background-color 0.2s !important;
    width: 100%;
    justify-self: center;

    &:hover {
    }

    .delete-card {
        color: #ff3c0060;
        transition: 0.2s;

        &:hover {
            color: #ff0000ce;
            background-color: #1783ff09;
        }
    }
}

.toy-prev-btns {
    .btn-link {
        width: 30%;
        height: 20%;
        margin: 0.6rem;
        padding: 0px;
        border-radius: 1%;

        a {
            color: gray;
            transition: 0.2s;
            
        }

        &:hover {
            background-color: #0077ff00;
            
        }
    }
}

.btn-link:hover a {
    color: #1783ffbb;


}

.form-filter {
    width: 150px;
}